import React from "react";
import { Row, Column } from "components/Grid.tsx";
import {
  EmbeddingContainer,
  Title,
  Primary,
  Content,
  Header,
  EpisodeIndex,
  CompanyName,
  Description,
  EpisodeCaption,
  DownloadButton,
  StyledLink,
  RowWrapper,
  LinksContainer,
  StyledInternalLink,
  TitleLink,
  PodcastPlayer,
} from "./elements";

const Episode = ({ episodeData, index }) => {
  const {
    embedLink,
    spotifyLink,
    googlePodcastsLink,
    caption,
    description,
    title,
    companyName,
    transcriptLink,
    blogTitle,
    smallButtons,
    applePodcastsLink,
  } = episodeData;

  return (
    <RowWrapper>
      <Row padding={30} gutterWidth={26} reversedFlexDirection>
        <Column span={1} />
        <Column span={4} spanMedium={10}>
          <Content>
            <Header>
              <Title>
                <TitleLink to={transcriptLink}>
                  {`${blogTitle} `}
                  <Primary>{title}</Primary>
                </TitleLink>
              </Title>
              <EpisodeIndex>{`00${index}`}</EpisodeIndex>
            </Header>
            <CompanyName>{companyName}</CompanyName>
            <Description>{description}</Description>
          </Content>
        </Column>
        <Column span={0} spanMedium={1} />
        <Column span={0} spanMedium={1} />
        <Column span={6} spanMedium={10}>
          <EmbeddingContainer>
            <PodcastPlayer
              src={embedLink}
              height="98px"
              width="90%"
              frameBorder="0"
              scrolling="no"
              title={`episode-${index}`}
            />
            <EpisodeCaption>{caption}</EpisodeCaption>
            <LinksContainer>
              {spotifyLink && (
                <StyledLink
                  target="_blank"
                  rel="noopener noreferrer"
                  href={spotifyLink}
                  smallButtons={smallButtons}
                >
                  <DownloadButton>SPOTIFY</DownloadButton>
                </StyledLink>
              )}
              {googlePodcastsLink && (
                <StyledLink
                  target="_blank"
                  rel="noopener noreferrer"
                  href={googlePodcastsLink}
                  smallButtons={smallButtons}
                >
                  <DownloadButton>GOOGLE PODCASTS</DownloadButton>
                </StyledLink>
              )}
              {applePodcastsLink && (
                <StyledLink
                  target="_blank"
                  rel="noopener noreferrer"
                  href={applePodcastsLink}
                  smallButtons={smallButtons}
                >
                  <DownloadButton>APPLE PODCASTS</DownloadButton>
                </StyledLink>
              )}
              {transcriptLink && (
                <StyledInternalLink to={transcriptLink}>
                  <DownloadButton>READ MORE</DownloadButton>
                </StyledInternalLink>
              )}
            </LinksContainer>
          </EmbeddingContainer>
        </Column>

        <Column span={1} />
      </Row>
    </RowWrapper>
  );
};

export default Episode;
