import React from "react";
import Episode from "components/podcast/episode";

import { Wrapper } from "./elements";

const Content = ({ data }) => (
  <Wrapper>
    {data.map((episode, index) => (
      <Episode
        episodeData={episode}
        index={index + 1}
        key={`episode-${index + 1}`}
      />
    ))}
  </Wrapper>
);

export default Content;
